import { useParams, Navigate, useNavigate, createSearchParams } from 'react-router-dom';
import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
//  import { EditorState, convertFromRaw } from 'draft-js';

import memoryapi from 'api/memory';
import MemoryContent from 'pages/memory/MemoryContent';
import SimpleSnackbar from 'components/simple_snackbar';
import MemoryContentLoading from 'components/memory/MemoryContentLoading';

async function getMemory(bucket, authURL, memoryId, handleMemoryCallback, handleMemoryError) {
  try {
    const memoryData = await memoryapi.viewMemory(bucket, authURL, memoryId);
    if (memoryData.status) {
      handleMemoryError(memoryData);
      return;
    }
    handleMemoryCallback(memoryData);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function ViewMemory({ bucket, authURL }) {
  const navigate = useNavigate();
  const params = useParams();
  const { memoryId } = params;
  const [memory, setMemory] = useState();
  const [snack, setSnack] = useState(null);
  const [snackMessage, setSnackMessage] = useState(null);
  const [snackError, setSnackError] = useState(null);

  const handleMemory = (mem) => setMemory(mem);

  const [memError, setMemError] = useState(null);

  const handleSnackMessage = (msg, error) => {
    setSnackMessage(msg);
    setSnackError(error);
  };

  const handleSnackBarClose = () => {
    setSnack(false);
    setSnackMessage(null);
    setSnackError(null);
  };

  const handleMemoryError = (results) => {
    if (results.status === 'unauthenticated') {
      handleSnackMessage('You have been logged out. Navigating to login page', true);
      setSnack(true);
      setInterval(() => setMemError('unauthenticated'), 3000);
    }

    if (results.status === 'forbidden') {
      handleSnackMessage(
        'You do not have access to this memory. Navigating to memories page',
        true
      );
      setSnack(true);
      setInterval(() => setMemError('forbidden'), 3000);
    }
  };

  useEffect(() => {
    getMemory(bucket, authURL, memoryId, handleMemory, handleMemoryError);
    const interval = setInterval(
      () => getMemory(bucket, authURL, memoryId, handleMemory, handleMemoryError),
      300000
    );
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    if (memError === 'unauthenticated') {
      const searchPar = createSearchParams({
        next: `memory/${memoryId}`,
      }).toString();

      navigate({ pathname: 'login', search: searchPar });
    }
  }, [memError]);

  if (memError === 'unauthenticated') {
    return <Navigate to="/login" />;
  }

  if (memError === 'forbidden') {
    return <Navigate to="/memories/own" />;
  }

  return memory ? (
    <>
      <MemoryContent
        memType="own"
        memTitle={memory.title}
        memDate={memory.date}
        memImages={memory.pictures.photos}
        memStory={memory.story}
        memoryID={memoryId}
        memThumbnail={memory.pictures.photos.findIndex((photo) => photo.title === memory.thumbnail)}
        bucket={bucket}
        authURL={authURL}
      />
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
        error={snackError}
      />
    </>
  ) : (
    <>
      <MemoryContentLoading />
      <SimpleSnackbar snackBarOpen={snack} message={snackMessage} onClose={handleSnackBarClose} />
    </>
  );
}

ViewMemory.propTypes = {
  bucket: PropTypes.string.isRequired,
  authURL: PropTypes.string.isRequired,
};
